html, body {
    overflow-x: hidden;
}

.pages {
    position: relative;
    width: 100%;
    height: 100vh;
}

@media(max-width: 850px) {
    .pages  {
        height: 100%;
    }
}

#fixed-box {
    position: fixed;
    top: 100px;
    width: 100%;
    height: 70%;
    background-image: url(/react_portfolio/static/media/flowers-sunset.65677784.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -2;
}

html {
    scroll-behavior: smooth;
}

#greeting-container {
    width: 100%;
    height: 100vh;
    background: rgb(255,255,255);
    background: linear-gradient(0deg, rgba(255,255,255,1) 23%, rgba(136,71,99,1) 94%);
}

#hello {
    font-family: 'Roboto Mono', monospace;
    font-weight: bold;
    font-size: 3em;
    color: white;
    text-shadow: 3px 3px grey;
}

#job-title {
    font-family: 'Kiwi Maru', serif;
    font-weight: bold;
    color: #3A2C30;
    text-shadow: 2px 2px grey;
}

#bottom-box {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 20%;
    background-color: white;
    z-index: 1;
}

#text-box {
    position: absolute;
    margin-top: 80px;
    margin-left: 10%;
    margin-right: 10%;
    width: 80%;
    height: 70%;
}

#typewriter-wrapper {
    position: relative;
    margin-top: 100px;
    text-align: center;
    color: #3A2C30;
    font-weight: bold;
    text-shadow: 1px 1px grey;
}

#flex-box {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
}

#welcome-wrapper {
    position: relative;
    max-width: 360px;
    text-align: center;
}

#welcome-text {
    font-family: 'Oxygen', sans-serif;
    padding-bottom: 2px;
    overflow: hidden;
    /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange;
    /* The typwriter cursor */
    white-space: nowrap;
    /* Keeps the content on a single line */
    margin: 0 auto;
    /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em;
    /* Adjust as needed */
    -webkit-animation: typing 3.5s steps(40, end), blink-caret .75s step-end infinite;
            animation: typing 3.5s steps(40, end), blink-caret .75s step-end infinite;
}

/* The typing effect */
@-webkit-keyframes typing {
    from {
        width: 0
    }
    to {
        width: 100%
    }
}
@keyframes typing {
    from {
        width: 0
    }
    to {
        width: 100%
    }
}

/* The typewriter cursor effect */
@-webkit-keyframes blink-caret {
    from, to {
        border-color: transparent
    }
    50% {
        border-color: orange;
    }
}
@keyframes blink-caret {
    from, to {
        border-color: transparent
    }
    50% {
        border-color: orange;
    }
}

#arrow-button-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

#arrow-button {
    position: absolute;
    bottom: -2%;
    overflow: visible;
    border-color: #3A2C30;
    border-radius: 10px;
    width: 15%;
    color: #3A2C30;
    z-index: 2;
}

@media (max-width: 700px) {
